import 'svelte/internal/disclose-version';
import * as $ from 'svelte/internal/client';
import { onMount } from 'svelte';
// Assuming these helpers are available in your project
import { addToDate } from '../src-helpers/date-helpers';
import { EU, iso8601 } from '../src-helpers/tinytime-templates';

var root = $.template(`<time class="timer svelte-2ifih4c76fo2z" title="Server time"> </time>`);

function Clock($$anchor, $$props) {
	$.push($$props, true);

	// Use $state for reactive date
	let date = $.state($.proxy($$props.date));
	// Use $derived for computed values
	let formattedDate = $.derived(() => EU.render($.get(date)));
	let isoDate = $.derived(() => iso8601.render($.get(date)));

	onMount(() => {
		const intervalId = setInterval(
			() => {
				$.set(date, addToDate($.get(date), { seconds: 1 }), true);
			},
			1000
		);

		// Cleanup interval on component destroy
		return () => {
			clearInterval(intervalId);
		};
	});

	var time = root();
	var text = $.child(time, true);

	$.reset(time);

	$.template_effect(() => {
		$.set_attribute(time, 'datetime', $.get(isoDate));
		$.set_text(text, $.get(formattedDate));
	});

	$.append($$anchor, time);
	$.pop();
}

if (import.meta.hot) {
	Clock = $.hmr(Clock, () => Clock[$.HMR].source);

	import.meta.hot.accept((module) => {
		$.cleanup_styles('svelte-2ifih4c76fo2z');
		module.default[$.HMR].source = Clock[$.HMR].source;
		$.set(Clock[$.HMR].source, module.default[$.HMR].original);
	});
}

export default Clock;

if (customElements.get('my-element') == null) customElements.define('my-element', $.create_custom_element(Clock, { date: {} }, [], [], true));
